img {
  user-select: none;
}

hr {
  border-color: $body-color;
  border-top-width: 3px;
  opacity: 0.15;
}

.products-row {
  @include media-breakpoint-down(xl) {
    margin-left: rem(-7.5px);
    margin-right: rem(-7.5px);
    > * {
      padding-left: rem(7.5px);
      padding-right: rem(7.5px);
    }
    .mb-col {
      margin-bottom: rem(15px);
    }
  }
  @include media-breakpoint-down(sm) {
    margin-left: rem(-4px);
    margin-right: rem(-4px);
    > * {
      padding-left: rem(4px);
      padding-right: rem(4px);
    }
    .mb-col {
      margin-bottom: rem(8px);
    }
  }
}

.noUi {
  &-connect {
    background: $secondary;
  }
  &-target {
    background: rgba($primary, 0.1);
    border-color: rgba($primary, 0.1);
    box-shadow: none;
  }
  &-horizontal {
    height: 5px;
  }
}

.noUi-horizontal .noUi-tooltip {
  bottom: auto;
  top: 120%;
}
.noUi-horizontal .noUi-handle {
  height: 20px;
  border: 3px solid $secondary;
  border-radius: 20rem;
  background: $secondary;
  box-shadow: none;
  width: 20px;
  top: -9px;
  &:focus {
    outline: 0;
  }
}
.noUi-handle:before,
.noUi-handle:after {
  top: 50%;
  transform: translateY(-50%);
  display: none;
}
.noUi-horizontal .noUi-tooltip {
  font-size: rem(12px);
  padding: 0.2rem 0.4rem;
  line-height: 1;
  border-color: $gray-400;
}

.form-control.is-otp {
  border-radius: 0;
  border: 0;
  background: rgba($primary, 0.08);
  text-align: center;
  @include rfs(60px, min-height);
}

// IMAGES
.iMax {
  display: block;
  max-width: 100%;
}
.ifluid {
  width: 100%;
  height: auto;
}

.section {
  position: relative;
  width: 100%;
  height: auto;
}

a {
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  &:hover {
    color: $secondary !important;
  }
}

input,
textarea {
  height: rem(62px);
  font-size: rem(24px);
  font-weight: bold;
}

.ml-auto {
  margin-left: auto !important;
  @include media-breakpoint-down(md) {
    margin-left: 0rem !important;
  }
}

.ObContain {
  object-fit: contain !important;
}

@include media-breakpoint-up(lg) {
  .overflow-auto {
    overflow: hidden !important;
  }
}

.form-control {
  padding-right: 2rem;
  height: rem(70px);
  border-radius: rem(4px) !important;
}

.form-select {
  background-position: left 0.75rem center !important;
  height: rem(50px);
}

textarea {
  min-height: rem(200px);
}
.title {
  text-align: center;
  &-text {
    position: relative;
    display: inline-block;
    padding-bottom: 1rem;
    &::after {
      content: "";
      position: absolute;
      top: 100%;
      right: 50%;
      transform: translate(50%, 0%);
      width: 100%;
      height: 0.15rem;
      border-radius: 0.3rem;
      background-color: rgba(0, 0, 0, 0.1);
    }
    &::before {
      content: "";
      position: absolute;
      z-index: 9;
      top: 100%;
      right: 50%;
      transform: translate(50%, 0%);
      width: 40%;
      height: 0.15rem;
      border-radius: 0.3rem;
      background-color: $primary;
    }
  }
}

.swiper {
  &-pagination {
    position: relative;
    &-bullet {
      border-radius: 0.1rem;
      width: 1rem;
      height: 0.15rem;
      background-color: rgba(0, 0, 0, 0.4);
      transition: all 0.2s ease-in-out;
      &-active {
        width: 3rem;
        background-color: $primary;
      }
    }
  }
}

.text {
  &-gray {
    &-300 {
      color: $gray-300;
    }
    &-400 {
      color: $gray-400;
    }
    &-500 {
      color: $gray-500;
    }
  }
}

.appText {
  @include font-size(150px);
  @include media-breakpoint-down(md) {
    @include font-size(120px);
  }
}
