.btn {
  position: relative;
  display: inline-block;
  padding: 0.5rem 3rem !important;
  background-color: $white;
  color: $dark;
  border-radius: rem(8px);
  font-size: rem(22px) !important;
  font-weight: 500;
  -webkit-box-shadow: 8px 8px 0px 3px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 8px 8px 0px 3px rgba(0, 0, 0, 0.75);
  box-shadow: 8px 8px 0px 3px rgba(0, 0, 0, 0.75);
  transition: all 0.2s ease-in-out;
  &:hover {
    -webkit-box-shadow: 10px -10px 0px 3px rgba(255, 0, 99, 1);
    -moz-box-shadow: 10px -10px 0px 3px rgba(255, 0, 99, 1);
    box-shadow: 10px -10px 0px 3px rgba(255, 0, 99, 1);
  }
}
