.footer {
  &-details {
    width: rem(500px);
    height: rem(500px);
    max-width: 100%;
    border-radius: 50%;
    margin: 0rem auto;
    background-color: $white;
    @include lightenShadow();
    @include media-breakpoint-down(lg) {
      width: 100%;
      height: auto;
      padding: 3rem 2rem;
      border-radius: 2rem;
    }
  }
}
