.appProgress {
  width: 100%;
  height: rem(25px);
  outline: 0.1rem solid $white;
  border-radius: 3rem;
  transition: all 0.1s ease-in-out;
  @include media-breakpoint-down(md) {
    height: rem(15px);
  }
  &-content {
    height: 100%;
    background-color: $white;
    border-radius: 3rem;
    -webkit-box-shadow: 0px 0px 15px -1px rgba(255, 255, 255, 1);
    -moz-box-shadow: 0px 0px 15px -1px rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 15px -1px rgba(255, 255, 255, 1);
    transition: all 0.1s ease-in-out;
  }
  &:hover {
    background-color: rgba(205, 0, 99, 0.5);
    outline-color: rgba(205, 0, 99, 0.5);
    .appProgress {
      &-content {
        background-color: $secondary;
        -webkit-box-shadow: 0px 0px 15px -1px $secondary;
        -moz-box-shadow: 0px 0px 15px -1px $secondary;
        box-shadow: 0px 0px 15px -1px $secondary;
      }
    }
  }
}
