.appTwitter {
  padding: 1.5rem;
  background-color: $white;
  box-shadow: $box-shadow;
  border-radius: 1.5rem;
  margin: 1rem;
  transition: all 0.2s ease-in-out;
  &:hover {
    transform: translateY(-2rem);
  }
  &-image {
    width: rem(80px);
    height: rem(80px);
    border-radius: 50%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.01);
    margin-left: 1rem;
  }
  &-icon {
    color: #1ba0f0;
    margin-right: 2rem;
    .svgIcon {
      width: 1.7rem !important;
      height: 1.7rem !important;
    }
  }
}
