.appCard {
  display: block;
  height: 100%;
  position: relative;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.03);
  overflow: hidden;
  transition: 0.25s ease-in-out;
  border-radius: 16px;
  cursor: pointer;
  &-header {
    position: relative;
    overflow: hidden;
  }
  &-body {
    position: relative;
    padding: 1rem 0rem 2rem 0rem;
    text-align: center;
    &-start {
      > * {
        margin: 0;
      }
    }
    h4 {
      font-size: rem(22px);
    }
    p {
      font-size: rem(18px);
    }
    &-overlay {
      position: absolute;
      top: 0rem;
      left: 0rem;
      width: 100%;
      height: 100%;
      background-color: #585556;
      padding: 2rem;
      text-align: center;
      transform-origin: bottom;
      transform: scaleY(0);
      transition: all 0.2s ease-in-out;
      color: $white;
      ul {
        padding: 0rem;
        margin: 0rem;
        text-align: left;
        li {
          display: flex;
          align-items: center;
          border-bottom: 0.1rem solid rgba(255, 255, 255, 0.1);
          padding: 0.7rem 0rem;
          span {
            width: 2.5rem;
            height: 2.5rem;
            border-radius: 50%;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            background-color: #9b999a;
          }
          p {
            margin: 0rem;
            @include font-size(18px);
            padding: 0rem 0.3rem;
          }
        }
      }
    }
  }
  &-more {
    a {
      font-size: rem(19px);
      font-weight: 700;
      svg {
        transition: all 0.2s ease-in-out;
      }
      &:hover {
        svg {
          transform: translateX(0.5rem);
        }
      }
    }
    color: $secondary;
    padding: 0.5rem 0rem;
    &-current {
      a {
        color: $dark;
        @include font-size(16px);
      }
    }
  }

  &-title {
    transition: 0.25s ease-in-out;
    @include font-size(24px);
    font-weight: 400;
    margin-bottom: 0.2rem;
  }
  &-date {
    @include font-size(16px);
    font-weight: 300;
    margin-bottom: 0rem;
  }
  &-thumb {
    transition: 0.25s ease-in-out;
  }

  &:hover {
    .appCard {
      &-thumb {
        transform: scale(1.1);
        transition: 1.5s ease-in-out;
      }
    }
  }
  &.product {
    .appCard {
      &-body {
        text-align: center;
        @include padding(45px);
      }
      &-title {
        @include font-size(25px);
        font-weight: 700;
      }
    }
  }
  &:hover {
    .appCard {
      &-body {
        &-overlay {
          transform: scaleY(1);
        }
      }
      &-more {
        &-current {
          a {
            color: $white;
          }
        }
      }
    }
  }
}
