.header {
  position: fixed;
  width: 100%;
  height: auto;
  top: 0rem;
  left: 0rem;
  color: $white;
  padding: 1.3rem 0rem;
  z-index: 9999;
  padding-top: 4rem;
  @include media-breakpoint-down(md) {
    padding-top: 2rem;
  }
  &-links {
    display: flex;
    align-items: center;
    padding: 0rem;
    margin: 0rem;
    list-style: none;
    text-align: center;
    margin-left: 1rem;
    li {
      display: inline-block;
      a {
        position: relative;
        display: inline-block;
        padding: rem(8px) 1rem;
        margin: 0rem 0.5rem;
        font-weight: 600;
      }
    }
  }
}

.headroom {
  will-change: transform;
  transition: all 100ms linear;
  &--not {
    &-top {
      background: rgba(0, 0, 0, 0.2);
      backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      padding: 1.5rem 0rem;
      @include lightenShadow();
    }
  }
}
.headroom--pinned {
  transform: translateY(0%);
}
.headroom--unpinned {
  transform: translateY(-100%);
}
