// libs
@import "swiper/swiper-bundle.css";
@import "./overide.scss";
html {
  scroll-behavior: smooth;
  ::-moz-selection {
    background: $secondary;
  }

  ::selection {
    background: $secondary;
  }
  font-size: 16px;

  overflow-x: hidden;
}

body {
  overflow-x: hidden;
}

.theApp {
  padding-top: 8rem;
  @include media-breakpoint-down(md) {
    padding-top: 4rem;
  }
  &.dark {
    .header {
      background-color: $dark !important;
    }
  }
}
