.socials {
  display: flex;
  justify-content: center;
  margin: 0 -0.3em;
  list-style-type: none;
  align-items: center;
  padding: 0;
  @include font-size(20px);
  text-align: center;
  > li {
    display: inline-block;
  }

  a {
    color: $white;
    border-radius: 20rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    transition: 200ms ease-in-out;
    @include rfs(50px, height);
    @include rfs(50px, width);
    border-radius: 20rem;
    color: $white;
    margin-right: 0.8rem;
    svg {
      @include rfs(1.5rem, height);
      @include rfs(1.5rem, width);
    }

    &:hover {
      color: $primary;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
  &.is-dark {
    a {
      background: #ededed;
    }
  }
}
