.intro {
  &-text {
    font-size: rem(100px);
    @include media-breakpoint-down(sm) {
      font-size: rem(80px);
    }
  }
  &-developer {
    position: relative;
    width: rem(450px);
    height: rem(450px);
    margin-left: auto;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include media-breakpoint-down(md) {
      margin: 0rem auto;
      width: rem(400px);
      height: rem(400px);
    }
    @include media-breakpoint-down(sm) {
      margin: 0rem auto;
      width: rem(300px);
      height: rem(300px);
    }
    &-texture {
      &-1,
      &-2 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        border: 0.1rem solid $white;
        &::after {
          content: "";
          position: absolute;
          top: -0.75rem;
          left: 50%;
          width: 1.5rem;
          height: 1.5rem;
          border-radius: 50%;
          background-color: $white;
          transition: all 0.2s ease-in-out;
          @include media-breakpoint-down(sm) {
            top: -0.5rem;
            width: 1rem;
            height: 1rem;
          }
        }
      }
      &-1 {
        width: 100%;
        height: 100%;
        animation: spin 6s linear infinite;
      }
      &-2 {
        width: 85%;
        height: 85%;
        animation: spin 6s linear infinite;
        animation-delay: 3s;
      }
    }
    &-image {
      width: 70%;
      height: 70%;
      border-radius: 50%;
      overflow: hidden;
    }
    &:hover {
      .intro {
        &-developer {
          &-texture {
            &-1,
            &-2 {
              &::after {
                transform: scale(1.2);
                background-color: $secondary;
              }
            }
          }
        }
      }
    }
  }
}
.projects {
  &-childTitle {
    width: 20rem;
    max-width: 100%;
    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }
  &-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    &-item {
    }
  }
}

@keyframes spin {
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
