.mobileNav {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background: $gray-400;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style-type: none;
  z-index: ($zindex-fixed + 5);
  @include media-breakpoint-up(lg) {
    display: none;
  }
  > li {
    > a {
      font-size: rem(12px);
      color: #909090;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: rem(20px) rem(10px);
      transition: 0.25s ease-in-out;

      .svgIcon {
        font-size: rem(20px);
      }
      &:hover {
        color: $secondary;
      }
    }
  }
}
